<template>
  <div class="tab-control">
    <div v-for="(item,index) in titles" :key="index" class="tab-control-item" 
    :class="{active:index==currentIndex}" @click="itemClick(index)"> 
      <span>{{item}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name:'TabControl',
  props: {
    titles:{
      type:Array,
      default() {
        return []
      }
    }
  },
  data () {
    return {
      currentIndex:0
    }
  },
  methods: {
    itemClick(index){
      this.currentIndex = index
      // 内部往外传数据
      this.$emit('tabClick', index)
    }
  }
}
</script>

<style scoped>
.tab-control{
  display: flex;
  font-size: 15px;
}
.tab-control-item{
  flex: 1;
  text-align: center;
  height: 40px;
  line-height: 40px;
}
.tab-control-item span{
  padding: 5px;
}
.active{
  color: var(--color-high-text);
}
.active span{
  border-bottom: 3px solid var(--color-tint);
}
</style>